@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: #454545;
  background-image: url('https://i.imgur.com/5YAQW2a.png');
  background-repeat: repeat;
  background-size: 500px;
  width: 100%;
  height: 100%;
  display: flex;
}

.Content {
  min-height: 100vh;
  width: 100%;
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  color: white;
}

@media only screen {
#SideBar.show {
  transform-origin: 0 0;
  transform: translate(0vw, 0);
}
}

@media only screen {
#SideBar.hide {
  transform-origin: 0 0; 
  transform: translate(min(-15vw,-200px), 0);  
}

}
#SideBar {
  background-color: #3a3a3a;
  display: flex;
  flex-direction: column;
  position: fixed;
  margin: 0;
  transition: transform .5s ease-in-out;
  -moz-transition: transform .5s ease-in-out;
  border-right: 3px solid #333333 ;
  height: 100vh;
  width: max(15vw,200px);
}

.SideBar-logo {
  width: 40%;
  align-self: center;
  padding-top: 3vh;
}

.Title{
  display: flex;
  align-self: flex-start;
  width: 90vw;
  padding-top: 5vh;
  padding-left: 10vw;
  padding-bottom: 5vh;
  margin-bottom: 35px;
  align-items: center;
  font-size: calc(20px + 2vmin);
  font-family: 'Baloo Tamma 2', cursive;
  background-color: #3a3a3a;
  border-bottom: 3px solid #333333 ;
}

.Menu-icon {
  background-color: #454545;
  padding: 1vh;
  font-size: 40px;
  border: 2px solid #333333 ;
  border-radius: 20%;
  color: whitesmoke;
  margin-right: 5vw;
}

.Menu-icon:hover {
  background-color: #3f3f3f;
}

.Item {
  display: flex;
  align-items: center;
  margin-left: 10px;
  font-size: calc(5px + 2vmin);
  font-family: 'Baloo Tamma 2', cursive;
  color:whitesmoke;
  -webkit-user-select: none; /* Safari */        
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

.ItemIcon {
  padding-right: 10px;
  margin-bottom: 4px;
}

.ListItem {
  display: flex;
  align-items: center;
  cursor: pointer;
  height: 10vh;
  width:100%;
  border-bottom: 1px solid black;
}

.ListItem:hover {
  background-color: #353535;
}

.ListItem:active {
  background-color: #313131;
}

.List {
  display: inline-block;;
  flex-direction: column;
  list-style-type: none;
  margin-right: 10%;
}

.App-logo {
  width: 30%;
  align-self: center;
  pointer-events: none;
}

.App-header {
  height: 100%;
  width: 100%;
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #71e324;
}
@media (orientation: landscape){
.TextBox {
    text-align: justify;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 5vw;
    margin-right: 5vw;
    text-indent: 10%;
}
}

@media (orientation: portrait){
.TextBox {
    text-align: justify;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 5vw;
    margin-right: 5vw;
    text-indent: 10%;
}
}

@media (orientation: landscape){
.Text { 
    height: calc((2/3)*60vmin);
    overflow-y: scroll;
    justify-content: center;
    font-size: calc(7px + 2vmin);
    font-family: 'Baloo Tamma 2', cursive;
    color:whitesmoke;
    padding-left: 10%;
    padding-right: 2%;
}
}

@media (orientation: portrait){
.Text { 
    width: 70vmin;
    overflow-y: scroll;
    justify-content: center;
    font-size: calc(7px + 2vmin);
    font-family: 'Baloo Tamma 2', cursive;
    color:whitesmoke;
    margin-top: 10%;
    padding-right: 2%;
}
}

@media (orientation: landscape){
.Portrait {
    height: calc((2/3)*60vmin);
    width: 60vmin;
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2), 0 8px 22px 0 rgba(0, 0, 0, 0.19);
}
}

@media (orientation: portrait){
.Portrait {
    width: 70vmin;
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2), 0 8px 22px 0 rgba(0, 0, 0, 0.19);
}
}

.Text::-webkit-scrollbar {
    width: 12px;
}

/* Track */
.Text::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
}

/* Handle */
.Text::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgba(0,0,0,0.1); 
    box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}
.Text::-webkit-scrollbar-thumb:window-inactive {
    background: rgba(0,0,0,0.01); 
}
.SkillsBox {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    width: 80%;
